<template lang="pug">
    .teachers__item-content
        .teachers__item-content-container
            .teachers__item-buttons
                button(@click.prevent='teacherExpandEvent()' :id='teacher.id' v-if="!containerVisible").teachers__item-expand
                button(@click.prevent='teacherHideEvent()' v-if="containerVisible").teachers__item-hide
            .teachers__number {{index}}
            .teachers__name {{teacher.lastName}} {{teacher.firstName}}
            .teachers__department {{teacher.department.name}}
            .teachers__experience {{teacher.experience}}
        teacher-item-container(
            v-if='containerVisible'
            :teacher='teacher'
        )
</template>    
<script>
export default {  
    components: {
        teacherItemContainer: () => import("../../admin/teachers/teacher-item-container")
    },
    data() {
        return {
            containerVisible: this.teacherItemContainerVisible
        }
    },
    props: {
        teacher: Object,
        index: Number,
        teacherItemContainerVisible: Boolean
    },
    methods: {
        teacherExpandEvent(){
                this.containerVisible=!this.containerVisible
                let buttons = document.getElementsByClassName('teachers__item-expand')
                buttons.forEach(elem => {
                    elem.disabled=true
                });
                document.getElementById(this.teacher.id).removeAttribute('disabled')
            },
        teacherHideEvent() {
            this.containerVisible=!this.containerVisible
            let buttons = document.getElementsByClassName('teachers__item-expand')
            buttons.forEach(elem => {
                elem.disabled=false
            });
        }
    }
}
</script>
<style scoped lang="scss">
.teachers__item-content {
    width: 100%;
    display: flex;
    flex-direction: column;
}
.teachers__item-content-container {
    width: 100%;
    display: flex;
    background: #d0d2d3;
    padding: 4px 0;
    border-bottom: 1px dotted black;
    min-height: 2.5rem;
    align-items: center;
    margin-bottom: 5px;
}
.teacher__number {
    width: 10%;
}
.teacher__code, .teacher__subject, .teacher__class  {
    width: 23%; 
}
</style>